@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  line-height: 1;
  box-sizing: border-box;
}
a, img, button, input {
  display: block;
}
a, img, button {
  cursor: pointer;
}
button {
  background-color: transparent;
  border: none;
}
a {
  text-decoration: none;
}
button:focus, input:focus {
  outline: none;
}
ul {
  list-style-type: none;
}
.App {
  min-height: 100vh;
  background: #fafafa;
  color: #555459;
  font-family: 'Rubik', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  width: 1226px;
  margin: 0 0 0 0.35%;
  padding: 3rem 0;
}
.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.text-block {
  width: 54.32%;
}
.title-block {
  display: flex;
  align-items: center;
  margin-bottom: 43px;
}
.logo {
  width: 11.26%;
  margin-right: 19px;
}
.page-title {
  font-size: 30px;
  font-weight: 300;
}
.page-sub-title {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 68px;
}
.features-block {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
#ic_1 {
  width: 30px;
}
.form-block {
  width: 41.27%;
  background-color: #fff;
  border-radius: 15px;
  padding: 48px 38px;
  box-shadow: 0px 4px 20px 2px rgba(0, 0, 0, 0.1);
}
.form-title, .form-subtitle {
  text-align: center;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.02em;
  color: #535355;
}
.form-title {
  font-weight: 700;
  margin-bottom: 18px;
}
.form-subtitle {
  width: 90%;
  margin: 0 auto 32px;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
}
.agreement-block {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.agreement-block input {
  width: 19px;
  height: 19px;
  margin-right: 10px;
  background: transparent;
  border: 1px solid #555459;
  border-radius: 3px;
}
.main-text {
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #535355;
}
.main-text.flex {
  text-align: center;
}
.main-text a {
  display: inline-block;
  font-size: 16px;
  line-height: 19px;
  color: #3c8fef;
  margin-left: 3px;
}

@media screen and (max-width: 1367px){
  .container {
    width: 1140px;
    margin-left: 0;
  }
  .main-text {
    font-size: 14px;
    line-height: 16px;
  }
  .main-text a {
    font-size: 14px;
    line-height: 16px;
  }
  .agreement-block {
    margin-bottom: 10px;
  }
  .agreement-block input {
    width: 15px;
    height: 15px;
    margin-right: 8px;
    margin-bottom: 1px;
  }
  .agreement-block .main-text{
    width: 90%;
  }
}
@media screen and (max-width: 1199px) {
  .container {
    width: 960px;
  }
  .form-block {
    width: 41.27%;
    background-color: #fff;
    border-radius: 15px;
    padding: 34px 34px 24px;
    box-shadow: 0px 4px 20px 2px rgba(0, 0, 0, 0.1);
  }
  .agreement-block {
    display: flex;
    align-items: flex-start;
  }
  .agreement-block input {
    margin-bottom: 0px;
  }
  .agreement-block .main-text {
    text-align: center;
  }
  .form-title {
    font-weight: 700;
    margin-bottom: 10px;
  }
  .form-subtitle {
    margin: 0 auto 20px;
  }
  .title-block {
    margin-bottom: 23px;
  }
  .logo {
    width: 11.26%;
    margin-right: 15px;
  }
  .page-title {
    font-size: 24px;
  }
  .page-sub-title {
    font-size: 24px;
    margin-bottom: 48px;
  }
  #ic_1 {
    width: 25px;
  }
}

@media screen and (max-width: 1024px) {
  .App {
    height: auto;
  }
  .container {
    width: 720px;
  }
  .row {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .text-block {
    width: 70.32%;
  }
  .form-block {
    width: 55.27%;
  }
  .text-block {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .title-block {
    justify-content: center;
  }
  .page-sub-title {
    text-align: center;
    width: 75%;
    line-height: 24px;
  }
  .features-block {
    display: flex;
    justify-content: center;
  }
}
@media screen and (max-width: 767px) {
  .container {
    width: 540px;
  }
  .text-block {
    width: 100%;
  }
  .form-block {
    width: 75.27%;
  }
}
@media screen and (max-width: 576px) {
  .container {
    width: 350px;
  }
  .text-block {
    width: 100%;
  }
  .form-block {
    width: 100%;
  }
  .title-block {
    margin:0 0 23px 18px;
  }
  .logo {
    width: 11.26%;
    margin-right: 10px;
  }
  .page-title {
    font-size: 20px;
    line-height: 24px;
    width: 55%;
  }
  .page-sub-title {
    font-size: 20px;
    margin-bottom: 32px;
  }
  #ic_1 {
    width: 20px;
  }
  .agreement-block .main-text{
    width: 95%;
  }
}
@media screen and (max-width: 374px) {
  .container {
    width: 310px;
  }
}
